<template>
  <div class='singers'>
      <!--  -->
      <div class='wrapper' ref='singerWrapper'>
        <div class='content'>
            <ul>
              <li v-for='(item,index) in singers'
                  :key='index'
                  :ref='item.Findex'
              >  
                <!-- -->
                <h3>{{item.Findex}}</h3>
                <!-- -->
                <ol>
                  <li v-for='(sItem,sIndex) in item.list' 
                      :key='sIndex'
                      @click='goDetail(sItem.Fsinger_mid)'
                      >
                    <img v-lazy="sItem.avator" alt="">
                    <span>{{sItem.Fsinger_name}}</span>
                  </li>
                </ol>
              </li>
            </ul>
        </div>
      </div>
      <!--  -->
      <div class='quicklist'>
        <ul 
          @touchstart='touchStart'
          @touchmove='touchMove'
          @touchend='touchEnd'
        >
          <li v-for="(item,index) in quickData" 
              :key="index"
              @click='quickJump(item)'
              :class='selFindex==item?"sel":""'>

            {{item}}
          </li>
        </ul>
      </div>
      <!-- -->
      <transition
        enter-active-class="animated slideInRight"
        leave-active-class="animated slideOutRight"
      >
        <router-view></router-view>
      </transition>
  </div>
</template>
<script>
/*
1. v 
1.5 
2.
[
  {Findex:A,list:[{a1},{a2},{a3}]}
  {Findex:b,list:[{b1},{b2},{b3}]}
]

3. v
4. v
5.    v
6. item v 
    
  1.  quickdata
  2.   
  3.  
  4. 
7.       v
  1.
  2.
8. 
  a.
  b.  
  c. 
  4. +   
  5. scrollElement 

9.

*/ 
import BS from 'better-scroll'
import {getSingersData} from 'api/api.js'
import {nomalData}from './index'
export default {
  data(){
    return{
      singers:[],
      selFindex:'hot',//
    }
  },
  methods:{
    goDetail(Fsinger_mid){
      console.log('11111')
      this.$router.push(`/singer/${Fsinger_mid}`)
    },
    // 
    touchStart(e){
      // 140
      let y = e.touches[0].pageY
      // 
      let startCount=parseInt((y-140)/18)
      this.touch.y=y
      this.touch.startCount=startCount
      console.log('',y ,startCount)
    },
    touchMove(e){
      let moveY =e.touches[0].pageY
      let moveDis=moveY-this.touch.y //
      let count =parseInt(moveDis/18) //
      console.log('',moveDis,count)
      let moveIndex =this.touch.startCount + count  //
      // 
      if(moveIndex<0||moveIndex>this.quickData.length-1){ return false}
      let moveFindex =this.quickData[moveIndex]
      this.quickJump(moveFindex)
    },
    touchEnd(){
      console.log('')
    },
    quickJump(item){
      //  
      // 
      let dom = this.$refs[item][0]
      // console.log('',item,dom)
      // //  bs 
      this.Bs.scrollToElement(dom)
    },
    initBs(){
      let wrapper = this.$refs.singerWrapper
      this.Bs = new BS(wrapper,{probeType:3,click:true})
      //   
      let distance=[]
      for (const key in this.$refs) {
          if(key!=='singerWrapper'){
            distance.push(this.$refs[key][0].offsetTop)
          }
      }
    // 
    this.Bs.on('scroll',(pos)=>{
      let y =Math.abs(pos.y)
      console.log('',y)
      // 
      let scrollIndex = 0 //
      for (let index = 0; index < distance.length; index++) {
          if(y>=distance[index]&&y<distance[index+1]){
            scrollIndex=index
          }else if(y>=distance[distance.length-1]){
          //  
            scrollIndex = distance.length-1
          }
      }
        
      // 
      let scrollFindex=this.quickData[scrollIndex]
      console.log(scrollFindex)
      this.selFindex=scrollFindex
    })
    
    }
  },
  computed: {
    quickData(){
      // 
 
      let result = this.singers.map((item)=>{
        return item.Findex
      })
      console.log('',result)
      return result
    }
  },
  created() {
    //  this
    this.touch={y:0}
    // console.log(111)
   getSingersData().then((res)=>{
    //  
     let data =nomalData(res.data.list)
     this.singers=data
      //  
      console.log(this.singers)
      this.$nextTick(()=>{
        this.initBs()
      })
   })
  },
}
</script>
<style lang="less" scoped>
// alias ~
@import '~style/index.less';
.singers{
  position: fixed;
  top:88px;
  bottom: 0px;
  left:0px;
  right: 0px;
  .quicklist{
    position: absolute;
    right: 10px;
    top:40px;
    width: 20px;
    background: rgba(0,0,0,.3);
    font-Size:@fs-xs;
    padding: 20px 0 20px;
    color:rgba(255,255,255,.5);
    border-radius:10px; 
    text-align: center;
    ul{
      li{
        width: 20px;
        height: 18px;
      }
      .sel{
        color: @yellow;
      }
    }
  }
  .wrapper{
    height: 100%;  
    // background: red;
    overflow: hidden;
    .content{
      .w(375);
      h3{
        height: 30px;
        line-height: 30px;
        background: #333;
        color:rgba(255,255,255,0.5);
        font-size: @fs-s;
        padding-left:20px;
         

      }
      ol{
        li{
          .w(375);
          height: 70px;
          // background: pink;
          box-sizing: border-box;
          padding: 20px 0px 0px 30px;
          display: flex;
          align-items: center;
          img{
            width: 50px;
            height: 50px;
            border-radius:25px; 
          }
          span{
            padding-left:20px; 
            display: inline-block;
            height: 50px;
            line-height: 50px;
            font-size: @fs-s;
            color:rgba(255,255,255,.5);
          }
        }
      }
    }
    }
}
</style>